<template>
  <div class="container">
    <div v-if="lists.length !== 0">
      <div v-for="(item, name, index) in lists" :key="index" class="box-card">
        <div class="od-title">
          {{ name }}
        </div>
        <el-card
          class="od-content"
          v-for="(task, index) in item"
          :key="index"
          shadow="hover"
        >
          <el-row :gutter="20">
            <el-col :span="21">
              {{ task.title }}
            </el-col>
            <el-col :span="3" class="od-tool">
              <i class="el-icon-document-add" @click="to_today(task)"></i>
            </el-col>
          </el-row>
        </el-card>
      </div>
    </div>
    <div v-else class="no-task">
      还没有逾期计划呢！
    </div>
  </div>
</template>

<script>
import { getOverdueTask, overdueToToday } from "../../api/task.js";

export default {
  data() {
    return {
      lists: {}
    };
  },
  methods: {
    fetch_data() {
      getOverdueTask(this.$store.getters.token).then(response => {
        this.lists = response.data;
      });
    },
    to_today(task) {
      overdueToToday(this.$store.getters.token, {
        task_id: task.task_id
      }).then(() => {
        this.fetch_data();
        this.$notify({
          title: "系统通知",
          message: "已添加到今日计划中",
          type: "success"
        });
      });
    }
  },
  created() {
    this.fetch_data();
  }
};
</script>

